import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { fetchSingleJoke } from "../../fetch/fetch";

const DUMMY_JOKE = [
	{
		id: 1,
		text: "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Ratione laborum tempora ab quidem quae eligendi ut eum sequi fugiat fugit.",
		author: "Ebrahim",
	},
	{
		id: 2,
		text: "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Ratione laborum tempora ab quidem quae eligendi ut eum sequi fugiat fugit.",
		author: "Ebrahim",
	},
	{
		id: 3,
		text: "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Ratione laborum tempora ab quidem quae eligendi ut eum sequi fugiat fugit.",
		author: "Ebrahim",
	},
];
let joke = {};
const JokeDetail = () => {
	const { jokeId } = useParams();
	const [loading, setLoading] = useState(false);

	useEffect(() => {
		setLoading(true);
		const loadJoke = async () => {
			return await fetchSingleJoke(jokeId);
		};
		loadJoke().then((response) => {
			joke = { ...response };
			console.log(joke);
			setLoading(false);
		});
	}, [jokeId]);

	return (
		<div className="joke-detail">
			{loading && <p>Loading the joke</p>}
			{!loading && (
				<React.Fragment>
					<div className="joke-text">{joke.content}</div>
					<div className="joke-text">{joke.author}</div>
					<button>Comments</button>
				</React.Fragment>
			)}
		</div>
	);
};

export default JokeDetail;
